<script lang="ts">
  import {
    Bot,
    BotMessageSquare,
    Sparkle,
    Sparkles,
    ThumbsUp,
    ThumbsDown,
  } from "lucide-svelte";
  import { formatMarkdown } from "../utils/markdown";
  import type { Message } from "../types";
  import { formatTimestamp } from "../utils/date";

  interface Props {
    message: Message & { id?: number };
    botIcon?: string;
    primaryColor: string;
    secondaryColor: string;
    onPrimaryColor: string;
    onSecondaryColor: string;
    showSources: boolean;
  }

  const {
    message,
    botIcon,
    primaryColor,
    secondaryColor,
    onPrimaryColor,
    onSecondaryColor,
    showSources,
  }: Props = $props();

  const content = message.content;
  const type = message.role;
  const isStreaming: boolean = !!message.isStreaming;
  const hasStarted: boolean = isStreaming && content.length > 0;
  const sources = message.sources;

  const iconMap = {
    Bot: Bot,
    BotMessageSquare: BotMessageSquare,
    Sparkle: Sparkle,
    Sparkles: Sparkles,
  } as const;

  const isIconUrl =
    botIcon &&
    (botIcon.startsWith("http://") || botIcon.startsWith("https://"));

  const BotIcon =
    !isIconUrl && botIcon && iconMap[botIcon as keyof typeof iconMap]
      ? iconMap[botIcon as keyof typeof iconMap]
      : Bot;

  const formattedContent: string =
    type === "ai" ? formatMarkdown(content) : content;

  let feedback = $state<"up" | "down" | null>(null);
  let showFeedbackDialog = $state<boolean>(false);
  let feedbackComment = $state<string>("");

  function sendFeedback(detail: any) {
    try {
      const ev = new CustomEvent("message-feedback", { detail });
      document.dispatchEvent(ev);
    } catch (e) {
      console.log("feedback", detail);
    }
  }

  function handleThumbUp() {
    if (feedback === "up") {
      feedback = null;
      sendFeedback({
        message,
        feedback: null,
        action: "delete",
        feedback_id: message?.feedback?.id ?? null,
      });
      return;
    }

    feedback = "up";
    showFeedbackDialog = false;
    feedbackComment = "";
    sendFeedback({ message, feedback: "up" });
  }

  function handleThumbDown() {
    if (feedback === "down") {
      feedback = null;
      showFeedbackDialog = false;
      feedbackComment = "";
      sendFeedback({
        message,
        feedback: null,
        action: "delete",
        feedback_id: message?.feedback?.id ?? null,
      });
      return;
    }

    feedback = "down";
    showFeedbackDialog = true;
  }

  function submitFeedback() {
    sendFeedback({ message, feedback: "down", comment: feedbackComment });
    showFeedbackDialog = false;
    feedbackComment = "";
  }

  function cancelFeedback() {
    sendFeedback({ message, feedback: "down" });
    showFeedbackDialog = false;
    feedbackComment = "";
  }

  function handleCopyClick(e: MouseEvent) {
    const target = e.target as HTMLElement | null;
    const button = target?.closest?.(".copy-code") as HTMLElement | null;
    if (!button) return;
    const raw = button.getAttribute("data-code") || "";
    const label = button.querySelector(".copy-label") as HTMLElement | null;
    const originalText = label ? label.textContent : "";
    if (navigator?.clipboard?.writeText) {
      navigator.clipboard
        .writeText(raw)
        .then(() => {
          if (label) label.textContent = "Copied";
          setTimeout(() => {
            if (label) label.textContent = originalText || "Copy Code";
          }, 1500);
        })
        .catch(() => {
          if (label) label.textContent = "Failed";
          setTimeout(() => {
            if (label) label.textContent = originalText || "Copy Code";
          }, 1500);
        });
    }
  }

  // Use Svelte runes: keep the bound element reactive and delegate clicks without on:click
  let markdownEl = $state<HTMLDivElement | null>(null);
  $effect(() => {
    const el = markdownEl;
    if (!el) return;
    const handler = (e: MouseEvent) => handleCopyClick(e);
    el.addEventListener("click", handler);
    return () => {
      el.removeEventListener("click", handler);
    };
  });

  // Populate initial feedback state from the incoming `message` if available
  $effect(() => {
    const fb = message?.feedback;
    if (!fb) return;
    feedback = fb.reaction === "positive" ? "up" : "down";
    showFeedbackDialog = false;
  });
</script>

<div
  class="message-container {type === 'human' ? 'human-message' : 'ai-message'}"
>
  <div
    class="message-content {type === 'human' ? 'human-content' : 'ai-content'}"
  >
    {#if type === "ai" && botIcon}
      <div
        class="avatar {isIconUrl ? 'icon-url' : ''}"
        style="background-color: {isIconUrl
          ? 'transparent'
          : primaryColor}; color: {isIconUrl ? 'inherit' : onPrimaryColor}"
      >
        {#if isIconUrl}
          <img src={botIcon} alt="Bot Icon" class="avatar-image" />
        {:else if BotIcon}
          <BotIcon size={20} strokeWidth={2} color={onPrimaryColor} />
        {/if}
      </div>
    {/if}

    <div class="message-body {type}-body">
      <div
        class="bubble {type}-bubble {isStreaming ? 'streaming' : ''}"
        style="--message-color: {type === 'human'
          ? secondaryColor
          : 'white'}; --text-color: {type === 'human'
          ? onSecondaryColor
          : '#1f2937'}"
      >
        {#if type === "human"}
          <div class="text">{content}</div>
        {:else if isStreaming && !hasStarted}
          <div class="thinking">
            <span>Thinking</span>
            <div class="dots-container">
              <span class="dot"></span>
              <span class="dot"></span>
              <span class="dot"></span>
            </div>
          </div>
        {:else}
          <div
            class="message-markdown markdown {isStreaming
              ? 'typing-animation'
              : ''}"
            bind:this={markdownEl}
          >
            {@html formattedContent}
          </div>
          {#if showSources && sources && sources.length > 0}
            <div class="sources-block">
              <div class="sources-title">Sources</div>
              <ul class="sources-list">
                {#each sources as s, i}
                  <li class="source-item" title={s.source || ""}>
                    {#if s.source}
                      <a
                        href={s.source}
                        target="_blank"
                        rel="noopener noreferrer">{s.title || s.source}</a
                      >
                    {:else}
                      <span>{s.title || "Untitled"}</span>
                    {/if}
                  </li>
                {/each}
              </ul>
            </div>
          {/if}
        {/if}
      </div>
      <div class="meta-row {type}-meta">
        <span class="timestamp">{formatTimestamp(message.timestamp)}</span>
        {#if type === "ai"}
          <div class="feedback-row" role="group" aria-label="Message feedback">
            {#if feedback !== "down"}
              <button
                class="feedback-btn up {feedback === 'up' ? 'selected' : ''}"
                aria-pressed={feedback === "up"}
                onclick={handleThumbUp}
                title="Positive"
              >
                <ThumbsUp size={14} />
              </button>
            {/if}

            {#if feedback !== "up"}
              <button
                class="feedback-btn down {feedback === 'down'
                  ? 'selected'
                  : ''}"
                aria-pressed={feedback === "down"}
                onclick={handleThumbDown}
                title="Negative"
              >
                <ThumbsDown size={14} />
              </button>
            {/if}
          </div>
        {/if}
      </div>
    </div>
  </div>
</div>

{#if showFeedbackDialog}
  <div class="feedback-dialog-overlay" role="dialog" aria-modal="true">
    <div class="feedback-dialog" role="document">
      <div class="feedback-dialog-title">
        We're sorry — can you tell us what went wrong?
      </div>
      <textarea
        class="feedback-textarea"
        placeholder="Optional: add details to help us improve..."
        bind:value={feedbackComment}
        rows={5}
      ></textarea>
      <div class="feedback-dialog-actions">
        <button class="btn btn-secondary" onclick={cancelFeedback}
          >Cancel</button
        >
        <button class="btn btn-primary" onclick={submitFeedback}>Submit</button>
      </div>
    </div>
  </div>
{/if}

