<script lang="ts">
  import {
    MessageSquare,
    MessageSquareMore,
    MessageCircle,
    MessageCircleMore,
    MessagesSquare,
    Bot,
    BotMessageSquare,
    Sparkle,
    Sparkles,
    X,
  } from "lucide-svelte";
  const props = $props<{
    isOpen: boolean;
    onToggle: () => void;
    buttonIcon?: string;
    showButtonText: boolean;
    buttonText: string;
    primaryColor: string;
    onPrimaryColor: string;
  }>();

  const iconMap = {
    MessageSquare,
    MessageSquareMore,
    MessageCircle,
    MessageCircleMore,
    MessagesSquare,
    Bot,
    BotMessageSquare,
    Sparkle,
    Sparkles,
  } as const;

  // Only treat an HTTPS URL as a custom image icon (per updated requirement)
  const isIconUrl = $derived(
    !!props.buttonIcon && props.buttonIcon.startsWith("https://")
  );

  const ButtonIcon = $derived(
    !isIconUrl
      ? iconMap[
          (props.buttonIcon as keyof typeof iconMap) || "MessageSquare"
        ] || MessageSquare
      : null
  );

  // Responsive icon sizing (desktop 24px, mobile <=480px 20px)
  let iconSize = $state(24);
  let iconScale = $state(1);
  function updateIconSize() {
    try {
      const w = typeof window !== "undefined" ? window.innerWidth : 9999;
      // extra: <=480 -> 18px, <=1024 -> 20px, >1024 -> 24px
      if (w <= 480) iconSize = 18;
      else if (w <= 1024) iconSize = 20;
      else iconSize = 24;
      iconScale = iconSize / 24;
    } catch (_) {
      iconSize = 24;
    }
  }
  updateIconSize();
  if (typeof window !== "undefined") {
    window.addEventListener("resize", updateIconSize);
    // Clean up when component is destroyed
    $effect(() => () => window.removeEventListener("resize", updateIconSize));
  }
</script>

<button
  id="chat-button"
  class:active={props.isOpen}
  class:with-text={props.showButtonText && !props.isOpen}
  class:with-icon-url={isIconUrl}
  onclick={() => {
    props.onToggle?.();
  }}
  aria-label={props.isOpen ? "Close chat" : "Open chat"}
  style="background-color: {props.primaryColor}; color: {props.onPrimaryColor};"
>
  {#if props.isOpen}
    <div
      class="close-icon"
      style="--icon-scale: {iconScale}; transform: scale(var(--icon-scale));"
    >
      <X size={iconSize + 4} strokeWidth={2} />
    </div>
  {:else}
    <div
      class="chat-icon"
      style="--icon-scale: {iconScale}; transform: scale(var(--icon-scale));"
    >
      {#if isIconUrl}
        <img
          src={props.buttonIcon}
          alt="Chat Icon"
          class="icon-image"
          style="width: {iconSize}px; height: {iconSize}px;"
        />
      {:else if ButtonIcon}
        <ButtonIcon size={iconSize} strokeWidth={2} />
      {/if}
    </div>
    {#if props.showButtonText && !props.isOpen}
      <span class="button-text">{props.buttonText}</span>
    {/if}
  {/if}
</button>

<style>
  #chat-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    min-width: 3.5rem;
    height: 3.5rem;
    padding: 0;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    box-shadow:
      0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    /* Animate size/padding/gap changes when breakpoints change */
    transition:
      min-width 180ms ease,
      height 180ms ease,
      padding 180ms ease,
      gap 180ms ease,
      border-radius 180ms ease;
    z-index: 10000;
  }

  #chat-button.with-text {
    padding: 0 1.25rem;
    border-radius: 100px;
  }

  .button-text {
    /* Responsive text: clamp(min, fluid, max) so it scales smoothly between viewports */
    /* min 14px, fluid around viewport width, max 18px for large screens */
    font-size: 18px;
    white-space: nowrap;
    transition: font-size 180ms ease;
  }

  /* icon wrappers get a scale variable so we can animate size changes smoothly */
  .chat-icon,
  .close-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 180ms ease;
    transform-origin: center;
    /* default scale is 1; set inline via style attribute when needed */
  }

  /* image transition for smooth width/height change */
  .icon-image {
    transition:
      width 180ms ease,
      height 180ms ease;
    display: block;
  }

  #chat-button:hover {
    transform: translateY(-1px);
  }

  /* Tablet and below: ensure text is reduced for mid-size screens and reduce button size */
  @media (max-width: 1024px) {
    .button-text {
      font-size: 16px;
    }

    /* make the button slightly smaller on tablet and laptop widths */
    #chat-button {
      min-width: 3rem;
      height: 3rem;
      padding: 0 0.6rem;
      border-radius: 50%;
    }

    #chat-button.with-text {
      padding: 0 0.6rem;
    }
  }

  /* Mobile: reduce button size and typography to fit smaller screens */
  @media (max-width: 480px) {
    #chat-button {
      min-width: 2.6rem;
      height: 2.6rem;
      padding: 0 0.5rem;
      gap: 6px;
      border-radius: 9px;
    }

    #chat-button.with-text {
      padding: 0 0.5rem;
    }

    .button-text {
      font-size: 13px;
    }
  }

  /* Very small screens: make the button even more compact */
  @media (max-width: 360px) {
    #chat-button {
      min-width: 2.4rem;
      height: 2.4rem;
      padding: 0 0.5rem;
      border-radius: 8px;
    }

    /* hide text to preserve space */
    #chat-button.with-text .button-text {
      display: none;
    }
  }
</style>
