import { format } from "date-fns";

type DateInput = Date | string | number;

export function formatDateTime(date: DateInput): string {
    const localDate = new Date(date);
    if (isNaN(localDate.getTime())) return "-";
    return format(localDate, "dd-MM-yyyy hh:mm a");
}

export function formatDate(date: DateInput): string {
    const localDate = new Date(date);
    if (isNaN(localDate.getTime())) return "-";
    return format(localDate, "dd-MM-yyyy");
}

export function formatTime(date: DateInput): string {
    const localDate = new Date(date);
    if (isNaN(localDate.getTime())) return "-";
    return format(localDate, "hh:mm a");
}

export function formatTimestamp(timestamp: DateInput | null | undefined): string {
    if (timestamp === null || typeof timestamp === "undefined") return "-";
    const date = new Date(timestamp as DateInput);
    if (isNaN(date.getTime())) return "-";
    const now = new Date();
    const diffInMinutes = Math.floor((now.getTime() - date.getTime()) / (1000 * 60));
    if (diffInMinutes < 1) return "Just now";
    if (diffInMinutes < 60) return `${diffInMinutes}m ago`;
    const diffInHours = Math.floor(diffInMinutes / 60);
    if (diffInHours < 24) return `${diffInHours}h ago`;
    return formatDateTime(date);
}
