import { mount } from 'svelte';
import App from './App.svelte';
import appCss from './assets/app.css?inline';
import hljsCss from 'highlight.js/styles/a11y-light.css?inline';
import type { WidgetSettings } from './lib/types';
import ChatButton from './lib/components/ChatButton.svelte';
import { widgetApi, chatApi } from './lib/utils/api';

// Explicit props and instance typing to satisfy TS without relying on Svelte's inferred component props
type ChatButtonPublicProps = {
    isOpen: boolean;
    onToggle: () => void;
    buttonIcon?: string;
    showButtonText: boolean;
    buttonText: string;
    primaryColor: string;
    onPrimaryColor: string;
};

type ChatButtonInstance = {
    update: (props: Partial<ChatButtonPublicProps>) => void;
    destroy: () => void;
};

// Export the widget class as a named export
export class FluentBotChatWidget {
    private container: HTMLElement | null = null;
    private buttonContainer: HTMLElement | null = null;
    private isOpen: boolean = false;
    private isMaximized: boolean = false;
    private clickHandler: (event: MouseEvent) => void;
    private maximizeHandler: () => void;
    private minimizeHandler: () => void;
    private closeHandler: () => void;
    private feedbackHandler: (event: Event) => void;
    private settings: WidgetSettings | null = null;
    private chatButtonInstance: ChatButtonInstance | null = null;

    constructor(botId: string) {
        this.clickHandler = this.handleClickOutside.bind(this);
        this.maximizeHandler = this.maximizeWidget.bind(this);
        this.minimizeHandler = this.minimizeWidget.bind(this);
        this.closeHandler = () => {
            if (this.isOpen) {
                this.toggleWidget();
            }
        };
        this.feedbackHandler = this.handleMessageFeedback.bind(this);
        this.initialize(botId);
    }

    private async fetchSettings(botId: string): Promise<WidgetSettings | null> {
        try {
            const response = await widgetApi.get(`/api/bots/${botId}/widget-settings`);
            return response.data.data;
        } catch (error) {
            console.error('Error fetching widget settings:', error);
            return null;
        }
    }

    private handleClickOutside(event: MouseEvent) {
        const path = event.composedPath();

        // Check if click is outside the widget
        const isOutside = !path.some(element =>
            element === this.container ||
            element === this.buttonContainer ||
            (element instanceof HTMLElement && (
                element.closest('#fluent-bot-chat-widget') === this.container ||
                // Treat clicks on the chat button as inside
                element.closest('#chat-button') !== null
            ))
        );

        if (isOutside && this.isOpen) {
            this.toggleWidget();
        }
    }

    private toggleWidget() {
        const prev = this.isOpen;
        this.isOpen = !this.isOpen;

        if (this.container) {
            if (this.isOpen) {
                this.container.style.display = 'flex';
                // Trigger animation after display is set
                setTimeout(() => {
                    this.container!.style.opacity = '1';
                    this.container!.style.transform = 'translateY(0) scale(1)';
                }, 0);
            } else {
                this.container.style.opacity = '0';
                this.container.style.transform = 'translateY(20px) scale(0.95)';
                // Hide after animation (match transition duration)
                setTimeout(() => {
                    this.container!.style.display = 'none';
                }, 600);
            }
        }

        // Keep ChatButton state in sync so the X icon shows when open
        if (this.chatButtonInstance) {
            if (typeof this.chatButtonInstance.update === 'function') {
                this.chatButtonInstance.update({ isOpen: this.isOpen });
            } else {
                // Fallback: destroy and re-mount to reflect new props
                try {
                    if (typeof this.chatButtonInstance.destroy === 'function') {
                        this.chatButtonInstance.destroy();
                    }
                } catch (e) {

                }
                this.chatButtonInstance = null;
                if (this.buttonContainer) {
                    // Ensure previous button DOM is removed before remounting
                    try {
                        this.buttonContainer.innerHTML = '';
                    } catch { }
                    const mountOptions: any = {
                        target: this.buttonContainer,
                        props: {
                            isOpen: this.isOpen,
                            onToggle: () => this.toggleWidget(),
                            buttonIcon: this.settings?.button_icon,
                            showButtonText: this.settings?.show_button_text,
                            buttonText: this.settings?.button_text,
                            primaryColor: this.settings?.primary_color,
                            onPrimaryColor: this.settings?.on_primary_color,
                        }
                    };
                    this.chatButtonInstance = mount(ChatButton, mountOptions) as ChatButtonInstance;

                }
            }
        }
    }

    private createStyles() {
        const style = document.createElement('style');
        style.textContent = `
            :host {
                position: fixed;
                bottom: 20px;
                z-index: 9999;
                display: flex;
                flex-direction: column;
            }

            :host(.align-right) {
                right: 20px;
                align-items: flex-end;
            }

            :host(.align-left) {
                left: 20px;
                align-items: flex-start;
            }

            #fluent-bot-chat-widget {
                width: 20vw;
                min-width: 400px;
                height: 55vh;
                min-height: 600px;
                overflow: hidden;
                display: none;
                flex-direction: column;
                position: relative;
                border-radius: 0.5rem;
                box-shadow: 
                    rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
                    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
                margin-bottom: 1rem;
                opacity: 0;
                transform: translateY(20px) scale(0.95);
                transform-origin: bottom;
                transition: opacity 0.7s ease,
                            transform 0.7s cubic-bezier(0.34, 1.56, 0.64, 1),
                            width 0.7s ease,
                            height 0.7s ease,
                            max-height 0.7s ease;
                will-change: width, height, transform, opacity;
                font-family: inherit;
            }

            :host(.align-right) #fluent-bot-chat-widget {
                transform-origin: bottom right;
            }

            :host(.align-left) #fluent-bot-chat-widget {
                transform-origin: bottom left;
            }

            /* Maximized state */
            #fluent-bot-chat-widget.maximized {
                width: 55vw;
                height: 85vh;
                max-height: 85vh;
                animation: fb-maximize-pop 1s cubic-bezier(0.34, 1.56, 0.64, 1);
            }

            @keyframes fb-maximize-pop {
                0% { transform: translateY(0) scale(1); }
                60% { transform: translateY(0) scale(1.012); }
                100% { transform: translateY(0) scale(1); }
            }

            

            /* Mobile and small device styles */
            @media screen and (max-width: 640px) {
                :host {
                    bottom: 0;
                    left: 0 !important;
                    right: 0 !important;
                    width: 100%;
                    height: 100%;
                    align-items: stretch !important;
                }

                #fluent-bot-chat-widget {
                    width: 100%;
                    height: 100%;
                    max-height: 100%;
                    margin: 0;
                    border-radius: 0;
                    transform: translateY(100%);
                }

                :host(.align-right) #fluent-bot-chat-widget,
                :host(.align-left) #fluent-bot-chat-widget {
                    transform-origin: bottom;
                }

                #fluent-bot-chat-widget[style*="display: flex"] {
                    transform: translateY(0) !important;
                }

                /* Hide chat button when widget is open on mobile */
                #fluent-bot-chat-widget[style*="display: flex"] ~ #chat-button-container {
                    display: none;
                }

                /* Reset chat button container position when widget is closed */
                #chat-button-container {
                    position: fixed;
                    bottom: 20px;
                    z-index: 9999;
                }

                :host(.align-right) #chat-button-container {
                    right: 20px;
                }

                :host(.align-left) #chat-button-container {
                    left: 20px;
                }

                /* Ignore maximized on small screens - always take full screen */
                #fluent-bot-chat-widget.maximized {
                    width: 100%;
                    height: 100%;
                    max-height: 100%;
                }
            }

            ${hljsCss}
            ${appCss}
        `;
        return style;
    }

    private async initialize(botId: string) {
        // Fetch settings first
        this.settings = await this.fetchSettings(botId);

        // Don't initialize if no settings
        if (!this.settings) {
            console.warn('No widget settings found, widget will not be initialized');
            return;
        }

        // Create container
        const container = document.createElement('div');
        container.id = 'fluent-bot-chat-widget-container';
        container.classList.add(`align-${this.settings.button_alignment}`);

        // Fluid mode removed

        // Create shadow root
        const shadowRoot = container.attachShadow({ mode: 'open' });

        // Create chat container
        const chatContainer = document.createElement('div');
        chatContainer.id = 'fluent-bot-chat-widget';

        // Create button container
        const buttonContainer = document.createElement('div');
        buttonContainer.id = 'chat-button-container';

        // Add styles to shadow root
        shadowRoot.appendChild(this.createStyles());

        // Add elements to shadow root
        shadowRoot.appendChild(chatContainer);

        // Always add button container
        shadowRoot.appendChild(buttonContainer);

        // Add to document
        document.body.appendChild(container);

        // Store references
        this.container = chatContainer;
        this.buttonContainer = buttonContainer;

        // Add event listeners
        document.addEventListener('click', this.clickHandler, true);
        document.addEventListener('close-widget', this.closeHandler);
        document.addEventListener('maximize-widget', this.maximizeHandler);
        document.addEventListener('minimize-widget', this.minimizeHandler);
        // Listen for message feedback events dispatched from Svelte components
        document.addEventListener('message-feedback', this.feedbackHandler as EventListener);

        // Mount the Svelte apps with settings
        mount(App, {
            target: chatContainer,
            props: {
                settings: this.settings
            }
        });

        // Mount chat button
        const mountOptions: any = {
            target: buttonContainer,
            props: {
                isOpen: this.isOpen,
                onToggle: () => this.toggleWidget(),
                buttonIcon: this.settings.button_icon,
                showButtonText: this.settings.show_button_text,
                buttonText: this.settings.button_text,
                primaryColor: this.settings.primary_color,
                onPrimaryColor: this.settings.on_primary_color,
            }
        };
        this.chatButtonInstance = mount(ChatButton, mountOptions) as ChatButtonInstance;
    }

    private maximizeWidget() {
        if (!this.container || this.isMaximized) return;
        this.isMaximized = true;
        this.container.classList.add('maximized');
    }

    private minimizeWidget() {
        if (!this.container || !this.isMaximized) return;
        this.isMaximized = false;
        this.container.classList.remove('maximized');
    }

    destroy() {
        if (this.container) {
            const container = this.container.closest(
                '#fluent-bot-chat-widget-container'
            );
            if (container) {
                container.remove();
            }
        }
        if (this.chatButtonInstance && typeof this.chatButtonInstance.destroy === 'function') {
            this.chatButtonInstance.destroy();
            this.chatButtonInstance = null;
        }
        document.removeEventListener('click', this.clickHandler, true);
        document.removeEventListener('close-widget', this.closeHandler);
        document.removeEventListener('maximize-widget', this.maximizeHandler);
        document.removeEventListener('minimize-widget', this.minimizeHandler);
        document.removeEventListener('message-feedback', this.feedbackHandler as EventListener);
    }

    private async handleMessageFeedback(event: Event) {
        try {
            const ev = event as CustomEvent;
            const detail = ev.detail || {};

            // Expecting: { message, feedback: 'up'|'down'|null, comment?: string }
            const msg = detail.message || null;
            const feedback = detail.feedback || null;
            const comment = detail.comment || detail.comments || null;
            const action = detail.action || null;
            const feedbackId = detail.feedback_id || detail.feedbackId || null;

            if (action === 'delete') {
                // Delete persisted feedback if we have an id
                if (!feedbackId) {
                    console.warn('Delete feedback action received but no feedback id present', msg);
                    return;
                }
                try {
                    await chatApi.delete(`/feedbacks/${feedbackId}`);
                } catch (err) {
                    console.error('Failed to delete feedback', err);
                }
                return;
            }

            if (!msg || !feedback) {
                // Nothing to send (e.g. toggle-off without persisted id)
                return;
            }

            // Map widget feedback values to API values
            const reaction = feedback === 'up' ? 'positive' : feedback === 'down' ? 'negative' : null;
            if (!reaction) return;

            // Try to discover a numeric message id from the message object
            const messageId = (msg as any).id ?? (msg as any).message_id ?? null;
            if (!messageId) {
                console.warn('Feedback event missing message id, skipping send', msg);
                return;
            }

            const payload = {
                message_id: messageId,
                reaction,
                comments: comment,
            };

            // POST to fluent-ai feedback endpoint
            await chatApi.post('/feedbacks', payload);
            // optionally log success
            // console.info('Feedback sent', payload);
        } catch (err) {
            // Do not throw for UI; just log for debugging
            console.error('Failed to send feedback', err);
        }
    }
}

const widgetExists = () => document.querySelector(
    '#fluent-bot-chat-widget-container'
);

// Export the inject function
export const injectWidget = (botId: string) => {
    if (!widgetExists()) {
        new FluentBotChatWidget(botId);
    }
};
