import { chatStore, loadChatHistory, resetChatStore } from '../stores/chat';
import type { WidgetSettings } from '../types';

export class InitService {
    private settings: WidgetSettings;

    constructor(settings: WidgetSettings) {
        this.settings = settings;
    }

    async initialize() {
        // Support legacy key `currentChatId` but prefer `conversation_id` going forward.
        const legacyKey = 'currentChatId';
        const newKey = 'conversation_id';

        // If conversation_id exists, use it. Otherwise, check legacy key and migrate it.
        let storedConversationId = localStorage.getItem(newKey);
        if (!storedConversationId) {
            const legacy = localStorage.getItem(legacyKey);
            if (legacy) {
                storedConversationId = legacy;
                // Migrate to new key but keep legacy for safety (optional).
                localStorage.setItem(newKey, legacy);
                localStorage.removeItem(legacyKey);
            }
        }

        if (storedConversationId) {
            // Load the newest page (no cursor) with default limit
            await loadChatHistory(storedConversationId, 20, null);
        } else if (this.settings.first_message) {
            this.addInitialMessage();
        }
    }

    private addInitialMessage() {
        if (!this.settings.first_message) return;

        chatStore.update((store) => ({
            ...store,
            messages: [
                {
                    role: "ai",
                    content: this.settings.first_message!,
                    timestamp: new Date(),
                },
            ],
        }));
    }

    resetChat() {
        chatStore.update((store) => ({
            ...store,
            messages: [],
        }));
        // Remove the new conversation identifier
        localStorage.removeItem('conversation_id');
        resetChatStore();

        if (this.settings.first_message) {
            this.addInitialMessage();
        }
    }
} 