<script lang="ts">
  import { chatStore, sendMessage, loadChatHistory } from "../stores/chat";
  import Message from "./Message.svelte";
  import { onMount, onDestroy, tick } from "svelte";
  import { Send } from "lucide-svelte";
  import type { Message as MessageType } from "../types";

  const { settings } = $props();
  const APP_NAME: string =
    (import.meta.env.VITE_APP_NAME as string) || "fluentBot";
  const APP_URL: string =
    (import.meta.env.VITE_FLUENT_BOT_URL as string) || "#";
  let messageInput = $state("");
  // Placeholder typing animation state
  let placeholderText = $state("What do you want to know?");
  let _placeholderIndex = 0;
  let _typingActive = false;
  let _typingTimeout: ReturnType<typeof setTimeout> | null = null;
  let isPlaceholderPaused = $state(false); // pause when focused or user typed
  let chatContainer: HTMLDivElement;
  let textarea: HTMLTextAreaElement;
  let isLoadingMore = $state(false);
  let shouldStickToBottom = $state(true);
  let initialScrollDone = $state(false);
  let bottomAnchor: HTMLDivElement;

  let messages = $derived($chatStore.messages);
  let isLoading = $derived($chatStore.isLoading);
  let hasMore = $derived($chatStore.hasMore);
  let conversationId = $derived($chatStore.conversation_id);
  const hasUserMessage = $derived(messages.some((m) => m.role === "human"));

  const isNearBottom = () => {
    if (!chatContainer) return true;
    const threshold = 80; // px
    const { scrollTop, scrollHeight, clientHeight } = chatContainer;
    return scrollHeight - (scrollTop + clientHeight) <= threshold;
  };

  const scrollToBottom = (smooth = false) => {
    if (!chatContainer) return;
    if (bottomAnchor) {
      bottomAnchor.scrollIntoView({
        behavior: smooth ? "smooth" : "auto",
        block: "end",
      });
      return;
    }
    const top = chatContainer.scrollHeight;
    if (smooth) {
      chatContainer.scrollTo({ top, behavior: "smooth" });
    } else {
      chatContainer.scrollTop = top;
    }
  };

  const handleScroll = async () => {
    if (!chatContainer) return;

    // Track whether we should stick to bottom on future updates
    if (initialScrollDone) {
      shouldStickToBottom = isNearBottom();
    }

    if (isLoadingMore || !hasMore) return;

    const { scrollTop } = chatContainer;
    if (scrollTop === 0 && conversationId) {
      isLoadingMore = true;
      // Preserve scroll position: measure before we prepend older messages
      const prevScrollHeight = chatContainer.scrollHeight;
      const prevScrollTop = chatContainer.scrollTop;

      // Fetch older messages using cursor-based pagination
      await loadChatHistory(conversationId, 20, $chatStore.nextCursor);

      // Wait for DOM to update after Svelte store changes
      await tick();

      // Adjust scrollTop so the user's viewport stays at the same message
      const newScrollHeight = chatContainer.scrollHeight;
      const delta = newScrollHeight - prevScrollHeight;
      // Only adjust if content actually grew
      if (delta > 0) {
        chatContainer.scrollTop = prevScrollTop + delta;
      }

      isLoadingMore = false;
    }
  };

  onMount(() => {
    // Ensure we start at the bottom when the chat mounts
    requestAnimationFrame(() => scrollToBottom(false));
    // Observe size/layout changes and keep anchored to bottom if desired
    const ro = new ResizeObserver(() => {
      if (shouldStickToBottom) {
        scrollToBottom(false);
      }
    });
    if (chatContainer) ro.observe(chatContainer);

    return () => {
      ro.disconnect();
    };
  });

  const adjustTextareaHeight = () => {
    if (textarea) {
      textarea.style.height = "auto";
      const maxHeight = 120; // 5 lines * 24px (line-height)
      const newHeight = Math.min(textarea.scrollHeight, maxHeight);
      textarea.style.height = `${newHeight}px`;
    }
  };

  // Typing animation helpers
  const clearTypingTimeout = () => {
    if (_typingTimeout) {
      clearTimeout(_typingTimeout as any);
      _typingTimeout = null;
    }
  };

  const typeString = async (str: string) => {
    let out = "";
    for (let i = 0; i < str.length; i++) {
      if (!_typingActive || isPlaceholderPaused || messageInput.trim()) return;
      out += str[i];
      placeholderText = out + "|"; // show caret while typing
      await new Promise((res) => (_typingTimeout = setTimeout(res, 40)));
    }
    // finished typing, show full text for a bit
    placeholderText = out;
    await new Promise((res) => (_typingTimeout = setTimeout(res, 1400)));
    // delete
    for (let i = out.length; i >= 0; i--) {
      if (!_typingActive || isPlaceholderPaused || messageInput.trim()) return;
      placeholderText = out.slice(0, i) + (i ? "|" : "");
      await new Promise((res) => (_typingTimeout = setTimeout(res, 25)));
    }
    placeholderText = "";
  };

  const startTyping = async () => {
    const qs: string[] = (settings?.quick_questions || []).slice(0, 10);
    if (!qs || qs.length === 0) return;
    if (_typingActive) return;
    _typingActive = true;
    while (_typingActive) {
      if (isPlaceholderPaused || messageInput.trim()) {
        // if user typed or placeholder paused, wait briefly then continue
        await new Promise((res) => (_typingTimeout = setTimeout(res, 500)));
        continue;
      }
      const q = qs[_placeholderIndex % qs.length] || qs[0];
      await typeString(q);
      _placeholderIndex = (_placeholderIndex + 1) % qs.length;
      // small pause between entries
      await new Promise((res) => (_typingTimeout = setTimeout(res, 300)));
    }
  };

  const stopTyping = () => {
    _typingActive = false;
    clearTypingTimeout();
  };

  $effect(() => {
    adjustTextareaHeight();
  });

  // Start/stop the placeholder typing animation based on quick questions, focus, and user input
  $effect(() => {
    const hasQuick =
      settings && settings.quick_questions && settings.quick_questions.length;
    if (hasQuick && !messageInput.trim() && !isPlaceholderPaused) {
      startTyping();
    } else {
      // Fall back to default placeholder when stopping
      stopTyping();
      if (!messageInput.trim()) placeholderText = "What do you want to know?";
    }
  });

  onDestroy(() => {
    stopTyping();
  });

  // Auto-scroll when messages change or stream updates if the user was near the bottom
  $effect(() => {
    // Touch dependencies: list length and last message signature (for streaming)
    const len = messages.length;
    // Force a one-time initial scroll to bottom once messages first render
    if (!initialScrollDone && len > 0) {
      tick().then(() => {
        requestAnimationFrame(() => {
          scrollToBottom(false);
          shouldStickToBottom = true;
          initialScrollDone = true;
        });
      });
      return;
    }

    if (shouldStickToBottom) {
      requestAnimationFrame(() => scrollToBottom(false));
    }
  });

  const handleSubmit = async () => {
    if (!messageInput.trim()) return;

    const content = messageInput;
    messageInput = "";
    adjustTextareaHeight();

    // New outgoing messages should stick to bottom
    shouldStickToBottom = true;

    await sendMessage(content, settings);
  };

  // Handle quick question click: immediately send
  const handleQuickQuestion = (q: string) => {
    if (!q) return;
    messageInput = q;
    handleSubmit();
  };
</script>

<div class="chat-container">
  <!-- Chat Container -->
  <div
    bind:this={chatContainer}
    class="messages-container"
    onscroll={handleScroll}
  >
    {#if isLoadingMore}
      <div class="loading-more">
        <div class="loading-spinner"></div>
        <span>Loading previous messages...</span>
      </div>
    {/if}
    {#each messages as message, i (message.timestamp + "-" + i)}
      <Message
        message={message as MessageType}
        botIcon={settings.bot_icon}
        primaryColor={settings.primary_color}
        secondaryColor={settings.secondary_color}
        onPrimaryColor={settings.on_primary_color}
        onSecondaryColor={settings.on_secondary_color}
        showSources={settings.show_sources}
      />
    {/each}
    <div class="bottom-anchor" bind:this={bottomAnchor}></div>
  </div>

  {#if !hasUserMessage && settings.quick_questions && settings.quick_questions.length}
    <div class="quick-questions">
      {#each settings.quick_questions.slice(0, 3) as q, i}
        <button
          type="button"
          class="quick-question"
          onclick={() => handleQuickQuestion(q)}
          style="--theme-color: {settings.primary_color}"
        >
          {q}
        </button>
      {/each}
      <div class="ai-disclaimer">
        Responses are generated using AI and may contain mistakes.
      </div>
    </div>
  {/if}

  <!-- Input Area -->
  <form
    onsubmit={(e) => {
      e.preventDefault();
      handleSubmit();
    }}
    class="input-container"
    style="--theme-color: {settings.primary_color}; --hover-color: {settings.secondary_color}"
  >
    <textarea
      bind:this={textarea}
      bind:value={messageInput}
      placeholder={placeholderText}
      class="message-input"
      rows="1"
      oninput={adjustTextareaHeight}
      onfocus={() => {
        isPlaceholderPaused = true;
        stopTyping();
      }}
      onblur={() => {
        isPlaceholderPaused = false;
      }}
      onkeydown={(e) => {
        // If the user starts typing (printable keys, backspace, delete), pause the placeholder animation
        const printable = (k: string) =>
          k && (k.length === 1 || k === "Backspace" || k === "Delete");
        if (printable(e.key)) {
          isPlaceholderPaused = true;
          stopTyping();
        }

        // Handle Enter to submit (without Shift)
        if (e.key === "Enter") {
          if (!e.shiftKey) {
            e.preventDefault();
            handleSubmit();
          }
        }
      }}
      disabled={isLoading}
    ></textarea>
    <button
      type="submit"
      class="send-button"
      aria-label="Send message"
      disabled={isLoading || !messageInput.trim()}
      title="Send"
    >
      <Send size={18} strokeWidth={2} />
    </button>
  </form>

  {#if settings.show_branding}
    <div class="branding">
      Powered by <a
        href={APP_URL}
        target="_blank"
        rel="noopener noreferrer"
        class="branding-link"
        style="color: {settings.primary_color}"
        >{APP_NAME}
      </a>
    </div>
  {/if}
</div>

<style>
  .chat-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  .messages-container {
    flex: 1;
    overflow-y: auto;
    /* Reduced horizontal padding (was 0.75rem) to tighten message list */
    padding: 0.5rem 0.5rem;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    min-height: 0; /* Important for flex container */
  }

  .quick-questions {
    background-color: #f7f7f7;
    padding: 0.25rem 0.75rem 0.5rem 0.75rem;
    display: flex;
    flex-direction: column;
    gap: 0.15rem;
  }

  .quick-question {
    background: transparent;
    border: none;
    padding: 2px 0;
    text-align: left;
    font-size: 0.7rem; /* ~11px similar to vue preview */
    font-weight: 500;
    line-height: 1.35;
    color: var(--theme-color, #3b82f6);
    cursor: pointer;
    white-space: normal;
    word-break: break-word;
  }
  .quick-question:hover,
  .quick-question:focus-visible {
    text-decoration: underline;
    outline: none;
  }

  .input-container {
    flex-shrink: 0;
    border-top: 1px solid #e5e7eb;
    background-color: #f7f7f7;
    padding: 0.25rem;
    width: 100%;
    box-sizing: border-box;
    position: relative;
  }

  .message-input {
    width: 100%;
    padding: 0.5rem 2.25rem 0.5rem 0.5rem; /* add right padding for send button */
    border: 1px solid #e5e7eb;
    border-radius: 0.5rem;
    box-sizing: border-box;
    max-width: 100%;
    font-size: 0.875rem;
    resize: none;
    min-height: 2.5rem;
    max-height: 120px;
    line-height: 1.5;
    overflow-y: auto;
    transition: height 0.1s ease;
    display: block;
  }

  .message-input:focus {
    outline: none;
    border-color: var(--theme-color);
  }

  .message-input:disabled {
    background-color: #f9fafb;
    cursor: not-allowed;
  }

  .send-button {
    position: absolute;
    right: 0.25rem;
    top: 50%;
    transform: translateY(-50%);
    width: 2rem;
    height: 2rem;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--theme-color);
    background: transparent;
    cursor: pointer;
    transition:
      background-color 0.15s ease,
      transform 0.15s ease,
      opacity 0.15s ease;
  }

  /* Hover effect removed as requested */

  .send-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  .loading-more {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.5rem;
    color: #6b7280;
    font-size: 0.875rem;
  }

  .loading-spinner {
    width: 1rem;
    height: 1rem;
    border: 2px solid #e5e7eb;
    border-top-color: #3b82f6;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }

  /* Branding line below the input */
  .branding {
    text-align: center;
    color: black; /* slate-400 */
    font-size: 11px;
    margin: 0.1rem 0 0.1rem 0;
  }

  .branding-link {
    font-weight: 600;
    color: var(--theme-color, #3b82f6);
    text-decoration: none;
    margin-left: 0.25rem;
    font-size: inherit;
    display: inline-block;
  }

  /* Small muted disclaimer under quick questions */
  .ai-disclaimer {
    font-size: 0.7rem; /* ~11px */
    color: #6b7280; /* slate-500 */
    text-align: center;
    margin-top: 0.15rem;
  }
</style>
